@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400;500;600;700&family=Inter:wght@300;400;500;600;700&display=swap');

body {
    background: linear-gradient(135deg, #1e1e2e 0%, #181825 50%, #1e1e2e 100%);
    font-family: 'Inter', sans-serif;
    color: #cdd6f4;
    line-height: 1.6;
}

@layer base {
    img {
        @apply inline-block;
    }

    /* Custom scrollbar */
    ::-webkit-scrollbar {
        width: 8px;
    }

    ::-webkit-scrollbar-track {
        background: #181825;
    }

    ::-webkit-scrollbar-thumb {
        background: #cba6f7;
        border-radius: 4px;
    }

    ::-webkit-scrollbar-thumb:hover {
        background: #b4befe;
    }
}

@layer components {
    .gradient-text {
        background: linear-gradient(135deg, #cba6f7 0%, #b4befe 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
    }

    .card {
        @apply bg-[#313244]/50 backdrop-blur-sm border border-[#45475a] rounded-xl p-6 shadow-xl;
    }

    .skill-bar {
        @apply bg-[#45475a] rounded-full h-2 overflow-hidden;
    }

    .skill-progress {
        @apply bg-gradient-to-r from-[#cba6f7] to-[#b4befe] h-full rounded-full transition-all duration-1000 ease-out;
    }

    .hover-glow {
        @apply transition-all duration-300 hover:shadow-lg hover:shadow-[#cba6f7]/20;
    }
}
